.p-20 {
    background: linear-gradient(45deg, #4d4546, #ec7e8f);
    /* #ffbb00, #ffee00 */
    box-shadow: 0 0 40px rgba(0, 0, 0, .4) inset;
    border-radius: 50px;
    margin: 10px;
    transition: box-shadow 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
    transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
    transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
    max-width: 250px;
    transition: 1s;
  }
  
  .p-20:hover {
    box-shadow: 0px 14px 30px rgb(55, 55, 55);
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px)
  }

  .btn {
    position: relative;
    display: block;
  
    font-size: 14px;
    font-family: "montserrat";
    text-decoration: none;
    margin: 30px 0;
    border: 2px solid #ffffff;
    padding: 14px 60px;
    text-transform: uppercase;
    overflow: hidden;
    transition: 1s all ease;
    border-radius: 20px;
  }
  
  .btn::before {
    background: #ff0000;
    content: "";
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: -1;
    transition: all 0.6s ease;
  }
  
  .btn4::before {
    width: 100%;
    height: 0%;
    -webkit-transform: translate(-50%, -50%) rotate(-45deg);
            transform: translate(-50%, -50%) rotate(-45deg);
  }
  
  .btn4:hover::before {
    height: 380%;
    background-color: #ebff10;
    border-radius: 10px;
    padding: 5px;
    box-shadow: 0px 10px 20px rgba($color: #000000, $alpha: 0.8);
  }
  
   /* Rating */

   .rating {
    margin: 0 0 1em 0;
    padding: 0;
    border: none;
}

/* Caption */

.rating__caption {
    margin-bottom: 0.5em;
    padding: 0;
}

/* Group */

.rating__group {
    position: relative;
    width: 10em;
    height: 2em;
    background-image: url(/heart.svg);
    background-size: 2em auto;
    background-repeat: repeat-x;
    
    
}

/* Star */

.rating__star {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    height: 2em;
    font-size: inherit;
    -webkit-appearance: none;
    appearance: none;
    background-size: 2em auto;
    background-repeat: repeat-x;
    
    background-color: rgba(255, 99, 71, 0);
}

.rating__star:focus {
    outline: none;
}

.rating__star:focus ~ .rating__focus {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
   
    outline-offset: 0.2em;
}

.rating__star:hover,
.rating__star:checked {
    background-image: url(/heart1.png);
}

.rating__star:hover ~ .rating__star {
    background-image: url(/heart.png);
}

/* Options */

.rating__star:nth-of-type(1) {
    z-index: 5;
    width: 2em;
}

.rating__star:nth-of-type(2) {
    z-index: 4;
    width: 4em;
}

.rating__star:nth-of-type(3) {
    z-index: 3;
    width: 6em;
}

.rating__star:nth-of-type(4) {
    z-index: 2;
    width: 8em;
}

.rating__star:nth-of-type(5) {
    z-index: 1;
    width: 10em;
}

