  /* анимация карточки */
  .wrapper1 {
    border-radius: 50px;
    max-width: 1200px;
    margin: -80px -90px -100px 0px;
  }
  
  .wrapper1 .card1 {
    width: 280px;
  }
  
  
  
  .wrapper1 .card1 .face1 {
  
    width: 250px;
    height: 200px;
    transition: 0.5s;
    background: #11aa79;
    /* #ffbb00, #ffee00 */
    box-shadow: 0 0 20px rgba(0, 0, 0, .4) inset;
    border-radius: 50px;
    margin: 10px;
  }
  
  .wrapper1 .card1 .face1.face12 {
    position: relative;
    background: linear-gradient(45deg, #f4405b, #ec7e8f);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
  }
  
  /* тут начинается анимация с выдвижением хуйни */
  .wrapper1 .card1:hover .face1.face12 {
  
    -webkit-transform: translateY(0);
  
            transform: translateY(0);
  }
  
  
  
  .wrapper1 .card1 .face1.face12 .content1 {
    opacity: 0.2;
    transition: 0.5s;
  }
  
  .wrapper1 .card1:hover .face1.face12 .content1 {
    opacity: 1;
  }
  
  .wrapper1 .card1 .face1.face12 .content1 img {
    max-width: 150px;
  }
  
  .wrapper1 .card1 .face1.face12 .content1 h3 {
    margin: 30px 0 0;
    padding: 0;
    color: #fff;
    text-align: center;
    font-size: 1.5em;
  }
  
  /* белая хуйня */
  
  .wrapper1 .card1 .face1.face21 {
    position: relative;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    box-sizing: border-box;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.8);
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
  
  .wrapper1 .card1:hover .face1.face21 {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  
  .wrapper1 .card1 .face1.face21 .content1 p {
    margin: 0;
    padding: 0;
  }
  
  .wrapper1 .card1 .face1.face21 .content1 a {
    margin: 10px 0 0;
    display: flex;
    text-decoration: none;
    font-weight: 900;
    color: #333;
    padding: 5px;
    border: 1px solid #333;
  }
  
  .wrapper1 .card1 .face1.face21 .content1 a:hover {
    background: #333;
    color: #fff;
  }
